<template>
  <div class="cluster servers">
    <div class="server-title" v-if="summary">
      <span>Cluster: {{summary.name}}<b>Total volume : {{summary.volumeCount}}</b></span>
      <el-button type="primary" class="ml50" @click="goServerList">Server List</el-button>
    </div>
    <div class="server-tree">
      <div class="text-center">
        <vue2-org-tree
          name="test"
          :data="data"
          :horizontal="horizontal"
          :collapsable="collapsable"
          :label-class-name="labelClassName"
          :render-content="renderContent"
          @on-expand="onExpand"
          @on-node-click="onNodeClick"
        />
      </div>
      <div class="photo"></div>
    </div>
  </div>
</template>

<script>
import baseGql from '../../graphql/server'
export default {
  name: 'servers',
  data () {
    return {
      summary: null,
      data: {
        id: 0,
        label: 'Cluster_Baud Storage',
        children: [
          {
            id: 1,
            label: 'Master',
            children: []
          },
          {
            id: 2,
            label: 'Matanode',
            children: []
          },
          {
            id: 3,
            label: 'Datanode',
            children: []
          }
        ]
      },
      horizontal: true,
      collapsable: true,
      expandAll: true,
      labelClassName: 'bg-white'
    }
  },
  methods: {
    renderContent (h, data) {
      return data.label
    },
    onExpand (e, data) {
      if ('expand' in data) {
        data.expand = !data.expand
        if (!data.expand && data.children) {
          this.collapse(data.children)
        }
      } else {
        this.$set(data, 'expand', true)
      }
    },
    // 点击选项执行的方法，可以用于跳转到其他链接，注意一定要写协议头
    onNodeClick (e, data) {
      // console.log(data.label);
      if (data.url == null) {
        return false
      } else {
        window.open(data.url)
      }
    },
    collapse (list) {
      var _this = this
      list.forEach(function (child) {
        if (child.expand) {
          child.expand = false
        }
        child.children && _this.collapse(child.children)
      })
    },
    expandChange () {
      this.toggleExpand(this.data, this.expandAll)
    },
    toggleExpand (data, val) {
      var _this = this
      if (Array.isArray(data)) {
        data.forEach(function (item) {
          _this.$set(item, 'expand', val)
          if (item.children) {
            _this.toggleExpand(item.children, val)
          }
        })
      } else {
        this.$set(data, 'expand', val)
        if (data.children) {
          _this.toggleExpand(data.children, val)
        }
      }
    },
    goServerList () {
      this.$router.push({
        name: 'serverList'
      })
    },
    dataProcessing (data) {
      let newDataArr = []
      for (let eachItem of data) {
        const obj = {
          id: eachItem,
          label: eachItem.addr ? eachItem.addr : eachItem
          // url: 'https://www.google.com/'
        }
        newDataArr.push(obj)
      }
      return newDataArr
    },
    queryExpandAll () {
      const that = this
      const variables = {
        num: 10000
      }
      this.apollo.query(this.url.cluster, baseGql.queryExpandAll, variables).then((res) => {
        if (res) {
          that.summary = res.data.clusterView
          that.data.children[0].children = this.dataProcessing(res.data.masterList)
          that.data.children[1].children = this.dataProcessing(res.data.metaNodeList)
          that.data.children[2].children = this.dataProcessing(res.data.dataNodeList)
        }
        this.expandChange()
      })
    }
  },
  mounted () {
    this.queryExpandAll()
  }
}
</script>

<style scoped>
  .server-title {
    width: 100%;
    padding-bottom: 18px;
    border-bottom: 1px solid #D8D8D8;
  }
  .server-title b {
    margin-left: 40px;
    font-weight: normal;
  }
  .server-tree {
    position: relative;
    min-height: 700px;
  }
  .server-tree .photo {
    position: absolute;
    right: 10px;
    bottom: 10px;
    width: 318px;
    height: 220px;
    background: url("../../assets/images/photo.png");
    background-size: 318px 220px;
  }
</style>



